@import 'assets/stylesheets/global';
$screens: (
  $small-only: small,
  $medium-only: medium,
  $large-only: large,
  $xlarge-only: xLarge,
  $xxlarge-up: xxLarge
);

@each $screenSize, $className in $screens {
  @media #{$screenSize} {
    .#{$className} {
      display: none;
    }
  }
}




