@import 'typography';

h1,
figure,
ul,
a {
  margin: 0;
}

a:hover {
  cursor: pointer;
}

body {
  font-family: $font-family-sans-serif;
  -webkit-font-smoothing: antialiased;
}

* {
  box-sizing: border-box;
}



