@import 'assets/stylesheets/global';
.warning,
.error {
  transition: all 500ms ease;
  margin-left: ru(0.5);
  opacity: 0;
  margin-top: 0;
  max-height: 0;
}

.warning {
  color: color('brand-2');
}

.error {
  color: color('accent-2');
}

.visible {
  opacity: 1;
  margin-top: ru(.25);
  max-height: ru(2);
}



